@use '../abstracts/variables' as *;

.header {
    padding: 18px 0;
}

.header-logo {
    a {
        font: {
            weight: 700;
            size: 24px;
        }
        letter-spacing: 0.5px;
    }

    svg {
        width: 40px;
        height: 32px;
        color: var(--primary-color);
    }
}

.header-search-area {
    display: flex;
    align-items: center;
    border: 1px solid $borderColor;
    padding: 6px 8px;
    background-color: #fff;
    border-radius: $borderRaduis;
}

.header-social-links {
    .nav {justify-content: end;}
    a {
        padding: 0 8px;
    }
    svg {
        width: 20px;
        color: var(--dark-color, #000);
    }
}

.header-auth {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-left: 1px solid rgba(163, 163, 163, 0.5);
    position: relative;

    &::after {
        position: absolute;
        content: '';
        border: 4px solid;
        border-color: rgb(29, 29, 29) transparent transparent;
        left: 16px;
        top: 50%;
        transform: translateY(-50%);
    }

    .auth {
        display: flex;
        align-items: center;

        .auth-image {
            flex-shrink: 0;
            margin-left: 16px;

            img {
                $avatar-size: 38px; 
                width: $avatar-size;
                height: $avatar-size;
                border-radius: 50%;
                object-fit: cover;
            }
        }

        .auth-info {
            flex-grow: 1;
            font-size: 13px;
            text-align: right;

            h6 {
                font-size: 15px;
                margin-bottom: 4px;
                text-transform: capitalize;
            }

            span {
                font-size: 12px;
                font-weight: 300;
            }
        }
    }
}